import React from 'react'
import { Form } from 'react-final-form'
import { Field } from 'react-final-form-html5-validation'
import {
  Box,
  Button,
  ControlFeedback,
  FormGroup,
  Input,
  Label,
  Select,
  Textarea,
} from "smooth-ui";

import PARAMS from '../Constants';

const adapt = Component => ({
  input,
  meta: {error, touched },
  children,
  ...rest
}) => {
  children = (error && touched)? <ControlFeedback valid={false}>{error}</ControlFeedback> : "";
  return <div><Component {...input} {...rest} />{children}</div>
};

const AdaptedInput = adapt(Input);
const AdaptedSelect = adapt(Select);
const AdaptedTextarea = adapt(Textarea);

const OrderForm = ({onSubmit, initialValues, orderPrice}) => {

  return (
    <Form
      initialValues={initialValues}
      onSubmit={onSubmit}
      render={({ handleSubmit, reset, submitting, pristine, values }) => {

        const isUkrPost = values.delivery === "ukr_post";
        const isPaymentCard = values.delivery !== "nova_pochta_np";
        
        return (
        <form onSubmit={handleSubmit}>
    
          <FormGroup>
            <Field
              name="firstName"
              component={AdaptedInput}
              type="text"
              placeholder=" "
              required
              minLength={3}
              maxLength={20}
              tooShort="   "
              valueMissing=" , !"
              control
            />
          </FormGroup>
          <FormGroup>
            <Field
              name="lastName"
              component={AdaptedInput}
              type="text"
              placeholder=" "
              required
              minLength={3}
              maxLength={20}
              tooShort="   "
              valueMissing=" , !"
              control
            />
          </FormGroup>

          <FormGroup>
            <Field
              name="email"
              component={AdaptedInput}
              type="email"
              placeholder="E-mail"
              required
              valueMissing=" e-mail, !"
              pattern="[a-zA-Z0-9._]+@[a-zA-Z0-9]+\.[A-Za-z]{2,}(\.[A-Za-z]{2,})?"
              patternMismatch="E-mail   "
              control
            />
          </FormGroup>
          <FormGroup>
            <Label>    </Label>
            <Field
              name="delivery"
              component={AdaptedSelect}
              options={PARAMS.DELIVERY_TYPES}
              control
            />
          </FormGroup>      
          
          <div className="order-delivery">
            <FormGroup>
              <Field
                name="postIndex"
                component={AdaptedInput}
                type="text"
                placeholder={(isUkrPost) ? `` : ` `}
                required
                valueMissing={(isUkrPost) ? `  , !` : `  , !`}
                pattern={(isUkrPost) ? `[0-9]{5}` : `[0-9]{1,3}`}
                patternMismatch="  "
                maxLength={(isUkrPost) ? 5 : 3}
                control
              />
            </FormGroup>
            <FormGroup>
              <Field
                name="city"
                component={AdaptedInput}
                type="text"
                placeholder=" "
                required
                valueMissing="  , !"
                minLength={2}
                tooShort="  "
                control
              />              
            </FormGroup>              
            <FormGroup>
              <Field
                name="address"
                component={AdaptedInput}
                type="text"
                placeholder=""
                required
                valueMissing=" , !"
                minLength={10}
                tooShort="  "
                control
              />              
            </FormGroup>
            {!isUkrPost && (
              <FormGroup>
                <Field
                  name="phone"
                  component={AdaptedInput}
                  type="text"
                  placeholder="(068)-777-77-77"
                  required
                  valueMissing=" , !"
                  pattern="[\s+0-9()-]{10,18}"
                  patternMismatch="  "
                  control
                />              
            </FormGroup>
              )}
            <ControlFeedback valid={false}>
                {(isPaymentCard)? `  :    e-mail.` 
                  : `      .`}
            </ControlFeedback>
          </div>

          <FormGroup>
            <Label>  </Label>
              <Field
                name="notes"
                component={AdaptedTextarea}
                type="text"
                control
              />              
          </FormGroup>
          <div className="order-price">
              <p> : {orderPrice} </p>
              <p> : {PARAMS.DELIVERY_PRICE[values.delivery]} </p>
              <p>: {orderPrice + Number(PARAMS.DELIVERY_PRICE[values.delivery])} </p>
          </div>
          <Box margin="10px 0" justifyContent="flex-end">
            <Button 
              variant="primary"
              type="submit" 
              disabled={submitting || pristine}
              >
              
            </Button>
          </Box>
        </form>
      )}
      }
    />
)
}

export default OrderForm;



